.toggle-button-wrapper {
  top: 0;
  margin-bottom: 2em;
}

.content {
  padding: 10%;
  margin: 0 auto;
}

.dark-mode-toggle {
  display: flex;
  margin: 0 auto;
  & > button {
    font-size: 1.2em;
    background: none;
    border: none;
    color: #7e57c2;
    cursor: pointer;
    transition: color 0.3s ease;
    &:last-child {
      color: #7e57c2;
    }

    &:focus {
      outline: none;
    }
  }
}

.toggle-control {
  position: relative;
  padding: 0 4px;
  display: flex;
  align-items: center;
}

input[type="checkbox"].dmcheck {
  width: 50px;
  height: 15px;
  background: #58e76f;
  position: relative;
  border-radius: 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;

  &:checked + label {
    left: 40px;
  }

  &:focus-visible {
    outline: solid 2px #58e76f;
  }

  & + label {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    transition: all 0.3s ease;
    cursor: pointer;
    position: absolute;
    left: 2px;
    background: #fff;
    opacity: 0.9;
    background-color: #ffffff;
  }
}

